module.exports = {"disclaimer":["仁濟醫院(下稱「仁濟」)力求載於本網頁資料準確，惟所提供的內容只作參考之用。仁濟不會就任何錯誤、遺漏、或錯誤陳述或失實陳述(不論明示或默示的)承擔任何責任。對任何因使用或不當使用本網頁所載的資料而引致或所涉及的損失、毀壞或損害(包括但不限於相應而生的損失、毀壞或損害)，仁濟概不承擔任何責任。","一旦使用本網頁，即表示使用者無條件接受上述所載的免責條款以及任何仁濟不時在無須預先通知使用者的情況下而對上述條款所作出的修改及/或修訂。","請定期瀏覽此網頁查閱任何修改及/或修訂。"],"privacy":["仁濟醫院(下稱「仁濟」)遵從個人資料(私隱)條例，採用以下政策，以確保網頁使用者之個人資料得到保密。仁濟從網頁收集得來的資料乃源於瀏覽者自願提供的，在未得到網頁使用者同意時，資料僅用作有關慈善籌款及指定用途。","仁濟承諾絕不會向外披露任何個人資料，亦不會出售、租借或傳送給予其他機構。","ST資料收集及使用","仁濟網頁伺服器將只記錄瀏覽者的網域名稱及所瀏覽的網頁，不會收集任何足以識辨瀏覽者身份的資料。這些資料只作統計每日瀏覽網頁的人數及所使用瀏覽器的類別，以便日後優化仁濟網頁之用。","ST儲存及取用","所有經由網頁提供予以仁濟的個人資料，在伺服器上取用是受到限制的。只有需要執行合法行動的員工或代理人才可使用。仁濟保留可以透過具機密指引約束的代理人來進行存取的權利。","ST登記","用戶需使用院友帳戶及院友密碼登入，所有資料以加密方式在互聯網上傳送，如需了解登記資料請聯絡 24086639。","ST更新 / 刪除個人資料","如欲更新 / 刪除您的個人資料，請以電郵通知網頁管理員","websiteadmin@yanchai.org.hk。","請定期瀏覽此網頁查閱任何修改及/或修訂。"],"copyright":["除註明外，仁濟醫院(下稱「仁濟」)是「仁濟心連心網站」所有內容(包括但不限於所有文本、數據、圖像及多媒體的展示等) 的版權擁有人。","所有載於本網站內由本院擁有版權的內容及資訊，可供自由瀏覽、列示、下載、列印、發佈或複製作非商業用途，但必須保留網站內容原來的格式，及須註明有關內容原屬仁濟所有，並複印此版權告示於所有複製本內。仁濟保留所有本版權告示沒有明確授予的權利。任何人士需要使用前述的內容作任何其他並非上述所批准的用途，須事先得到仁濟的書面同意。如欲申請，請以電郵通知網頁管理員 websiteadmin@yanchai.org.hk。","以上所給予的批准並不引伸至任何其他與「仁濟心連心網站」連結的網站，或在「仁濟心連心網站」內標明由第三者擁有版權的內容。如你需要使用該等內容，必須取得有關版權擁有人的授權或批准。","請定期瀏覽此網頁查閱任何修改及/或修訂。"]}

